<template>
  <v-row align="center">
    <v-form ref="form" v-model="valid">
      <v-text-field
        v-model="titre"
        :counter="10"
        :rules="titreRules"
        label="Titre"
        required
      ></v-text-field>
      <p>{{ titre }}</p>
      <v-text-field
        v-model="soustitre"
        :counter="20"
        :rules="soustitreRules"
        label="Sous-Titre"
        required
      ></v-text-field>

      <v-col cols="12" md="12">
        <v-textarea
          v-model="texte"
          :counter="100"
          :rules="texteRules"
          solo
          name="input-7-4"
          label="Texte"
        ></v-textarea>
      </v-col>

      <v-file-input
        label="Ajouter une Photo"
        filled
        prepend-icon="mdi-camera"
      ></v-file-input>

      <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">
        Valider
      </v-btn>

      <v-btn color="error" class="mr-4" @click="reset">
        Effacer formulaire
      </v-btn>
    </v-form>
  </v-row>
</template>

<script>
export default {
  name: "FormAdmin",
  data() {
    return {
      valid: true,
      titre: "",
      titreRules: [
        v => !!v || "Le titre est obligatoire !",
        v =>
          (v && v.length <= 10) ||
          "Le titre ne doit pas dépasser 10 caractères."
      ],
      soustitre: "",
      soustitreRules: [
        v => !!v || "Le sous-titre est obligatoire !",
        v =>
          (v && v.length <= 20) ||
          "Le titre ne doit pas dépasser 20 caractères."
      ],
      texte: "",
      texteRules: [
        v => !!v || "Le texte est obligatoire !",
        v =>
          (v && v.length <= 100) ||
          "Le titre ne doit pas dépasser 100 caractères."
      ],
      url: "https://cdn.vuetifyjs.com/images/cards/sunshine.jpg",
      checkbox: false
    };
  },

  methods: {
    validate() {
      // Création objet événement
      let event = {
        title: this.titre,
        soustitle: this.soustitre,
        texte: this.texte,
        url: this.url
      };

      this.$store.dispatch("CreateOneEvent", event).then(() => {
        this.reset();
      });
    },

    reset() {
      this.$refs.form.reset();
    }
  }
};
</script>
