<template>
  <div>
    <h1>Espace Administrateur</h1>
    <div id="admin-container">
      <div id="admin-actualites">
        <h2>Evénements</h2>
        <v-col v-for="(item_event, i) in event" :key="i" cols="12">
          <v-card dark>
            <div class="d-flex flex-wrap justify-space-around">
              <div>
                <v-card-title
                  class="headline"
                  v-text="item_event.title"
                ></v-card-title>

                <v-card-subtitle
                  v-text="item_event.soustitle"
                ></v-card-subtitle>
                <v-spacer></v-spacer>
                <p>{{ item_event.texte }}</p>
              </div>

              <v-avatar class="ma-3" size="125" tile>
                <v-img :src="item_event.imageUrl"></v-img>
              </v-avatar>
              <div class="my-2">
                <v-btn small color="error" @click="deleteEvent(item_event._id)"
                  >Supprimer</v-btn
                >
              </div>
            </div>
          </v-card>
        </v-col>
        <div>
          <p>Ajouter un évenement</p>
          <FormAdmin />
        </div>
      </div>
      <div id="admin-projets">
        <h2>Projets</h2>
        <v-col v-for="(item, x) in projet" :key="x" cols="12">
          <v-card dark>
            <div class="d-flex flex-wrap justify-space-around">
              <div>
                <v-card-title
                  class="headline"
                  v-text="item.title"
                ></v-card-title>

                <v-card-subtitle v-text="item.description"></v-card-subtitle>
                <v-spacer></v-spacer>
                <p>{{ item.date }}</p>
              </div>

              <v-avatar class="ma-3" size="125" tile>
                <v-img :src="item.imageUrl"></v-img>
              </v-avatar>
              <div class="my-2">
                <v-btn small color="error" @click="deleteProjet(item._id)"
                  >Supprimer</v-btn
                >
              </div>
            </div>
          </v-card>
        </v-col>
        <div>
          <p>Ajouter un projet</p>
          <ProjetForm />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FormAdmin from "@/components/FormAdmin";
import ProjetForm from "@/components/ProjetForm";

export default {
  name: "Dashboard",
  components: {
    FormAdmin,
    ProjetForm
  },

  methods: {
    deleteEvent: function(id) {
      let identifiant = id;
      this.$store.dispatch("deleteOneEvent", identifiant);
    },
    deleteProjet: function(id) {
      let identifiant = id;
      this.$store.dispatch("deleteOneProjet", identifiant);
    }
  },

  computed: {
    event() {
      return this.$store.state.evenements;
    },

    projet() {
      return this.$store.state.projets;
    }
  },

  created() {
    this.$store.dispatch("getAllEvent");
    this.$store.dispatch("getAllProjet");
  }
};
</script>

